[eboard-authority="viewer"] .eboard-toolbar-wrap,
[eboard-authority="operator"] .eboard-toolbar-wrap {
  display: none;
}
.eboard-toolbar {
  position: relative;
}
.eboard-toolbar-pencil-dot0 {
  width: 5px;
  height: 5px;
  background-color: black;
  border-radius: 999px;
}
.eboard-toolbar-pencil-dot1 {
  width: 5px;
  height: 5px;
  background-color: black;
  border-radius: 999px;
}
.eboard-toolbar-pencil-dot2 {
  width: 5px;
  height: 5px;
  background-color: black;
  border-radius: 999px;
}
.eboard-toolbar-wrap {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 2;
  width: 100%;
  height: 40px;
  margin-left: 10px;
}
.eboard-toolbar-item {
  margin: 5px 0;
  position: relative;
  width: 40px;
  height: 40px;
  display: inline-block;
  line-height: 40px;
  text-align: center;
  font-size: 20px;
  color: #000;
  cursor: pointer;
  vertical-align: middle;
  box-sizing: border-box;
  border: 1px solid transparent;
  border-radius: 1000px;
}
.eboard-toolbar-item:hover {
  border: 1px solid silver;
  background: #eeeeee;
}
.eboard-toolbar-item.active {
  border: 1px solid transparent;
  background: #eeeeee;
}
.eboard-toolbar-item1 {
  margin: 5px 3px 5px 4px;
  position: relative;
  width: 16px;
  height: 16px;
  display: inline-block;
  text-align: center;
  font-size: 16px;
  color: #000;
  cursor: pointer;
}
.eboard-toolbar-item1:hover {
  color: #ffffff;
}
.eboard-toolbar-item1.active {
  color: #ffffff;
}
.eboard-toolbar-expend > .eboard-icon-expend {
  position: absolute;
  right: 6px;
  bottom: 6px;
  font-size: 8px;
  width: 8px;
  height: 8px;
  line-height: 8px;
  border-radius: 100%;
}
.eboard-toolbar-expend > .eboard-icon-line {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 5px;
  margin: 0 auto;
  width: 20px;
  height: 2px;
}
.eboard-toolbar-huabi {
  margin-right: -5px;
}
.eboard-toolbar-huabi .eboard-toolbar-item1 {
  cursor: pointer;
  display: inline-block;
  vertical-align: middle;
  background-color: #000000;
  border-radius: 10px;
  margin: 10px;
}
.eboard-toolbar-huabi .eboard-toolbar-item1:hover {
  background-color: #ffffff;
}
.eboard-toolbar-huabi .eboard-toolbar-item1.active {
  background-color: #ffffff;
}
.eboard-toolbar-wenzi .eboard-toolbar-item1 {
  cursor: pointer;
  display: inline-block;
  margin: 10px 6px;
  color: #000000;
  font-weight: bolder;
  font-size: 16px;
}
.eboard-toolbar-wenzi .eboard-toolbar-item1:hover {
  color: #ffffff;
}
.eboard-toolbar-wenzi .eboard-toolbar-item1.active {
  color: #ffffff;
}
.eboard-toolbar-category {
  position: absolute;
  z-index: 20;
  background: #cccccc;
  padding: 15px 15px 120px;
  border-radius: 10px;
  left: 100%;
  top: 0;
  margin: 0;
  width: 148px;
}
.eboard-toolbar-category-line {
  position: absolute;
  bottom: 110px;
  left: 15px;
  right: 15px;
  height: 1px;
  background-color: #555555;
}
.eboard-toolbar-category-color {
  position: absolute;
  bottom: 10px;
  left: 13px;
  right: 13px;
}
.eboard-toolbar-category-color-item {
  cursor: pointer;
  display: inline-block;
  width: 20px;
  height: 20px;
  border-radius: 20px;
  margin: 5px 5px 0;
  padding: 2px;
  background-clip: content-box;
}
.eboard-toolbar-category-color-item:hover {
  border: 1px solid #666e7b;
}
.eboard-toolbar-category-color-item.active {
  border: 1px solid #666e7b;
}
.eboard-toolbar-picker {
  position: relative;
  border: 1px solid silver;
  margin-left: 10px;
}
.eboard-toolbar-picker input {
  visibility: hidden;
  position: absolute;
  width: 100%!important;
  height: 100%!important;
  left: 0!important;
  top: 0!important;
  overflow: hidden;
}
.eboard-toolbar-color-item {
  width: 40px;
  height: 40px;
  position: relative;
  display: inline-block;
}
.eboard-toolbar-color-item-inner {
  position: absolute;
  width: 60%;
  height: 60%;
  border-radius: 999px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.eboard-toolbar-color-item.active:after {
  position: absolute;
  content: "";
  width: 80%;
  height: 80%;
  border-radius: 999px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  border: 1px solid silver;
}
.eboard-toolbar-container {
  white-space: nowrap;
  border-bottom: 1px solid silver;
}
.eboard-toolbar-container .eboard-toolbar-item {
  display: inline-block;
}
.eboard-toolbar-select {
  position: relative;
  border: 1px solid silver;
  margin-left: 10px;
  width: 100px;
}
.eboard-toolbar-select select {
  display: none;
}



