.ps-scroll-not-hide{
    .ps__rail-y{
        opacity: 0.4;
    }
    .ps__rail-x{
        opacity: 0.4;
    }
}

.ps__rail-x{
    z-index: 98765;
}

.ps__rail-y{
    z-index: 98765;
}


.ps-scroll-disable{
    .ps__rail-x{
        visibility: hidden;
    }
    .ps__rail-y{
        visibility: hidden;
    }
}


