.tab-container {
  position: absolute;
  width: 100%;
  height: 30px;
}
.tab-container:after {
  position: absolute;
  width: 100%;
  bottom: -1px;
  border-bottom: 1px solid #bbbbbb;
  content: '';
  z-index: 10;
  left: 0;
}
.tab-item {
  display: inline-block;
  vertical-align: middle;
  height: 30px;
  font-size: 12px;
  line-height: 30px;
  min-width: 60px;
  max-width: 120px;
  color: #666666;
  cursor: pointer;
  margin-right: 2px;
  background-color: #eaeaea;
  position: relative;
}
.tab-item:hover .tab-remove {
  opacity: 1;
}
.tab-item-name {
  display: inline-block;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  text-align: center;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  padding: 0 10px;
  min-width: 60px;
  max-width: 120px;
}
.tab-item-calc {
  position: absolute;
  z-index: -1;
  opacity: 0;
}
.tab-active {
  color: #fff;
  background: #4c84ff;
}
.tab-active .tab-remove {
  opacity: 1;
  color: #fff;
}
.tab-add {
  display: inline-block;
  vertical-align: middle;
  width: 40px;
  height: 30px;
  font-size: 12px;
  line-height: 30px;
  text-align: center;
  box-sizing: border-box;
  background-color: #eaeaea;
  cursor: pointer;
}
.tab-add:hover {
  background-color: #4c84ff;
  color: #fff;
}
.tab-scroll {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  max-width: calc(100% - 40px);
  padding-right: 0;
  transition: max-width 240ms ease;
  white-space: nowrap;
  overflow: hidden;
}
.tab-scroll-pager {
  max-width: calc(100% - 94px);
}
.tab-scroll-bar {
  position: relative;
  transform: translateX(0);
  transition: transform 240ms ease;
}
.tab-prev,
.tab-next {
  display: inline-block;
  width: 27px;
  line-height: 30px;
  font-size: 12px;
  color: #666666;
  text-align: center;
  vertical-align: middle;
  position: absolute;
  right: 40px;
  top: 0;
  box-sizing: border-box;
  height: 30px;
  background: #eaeaea;
  cursor: pointer;
}
.tab-prev-disable,
.tab-next-disable {
  cursor: not-allowed;
  pointer-events: none;
  color: #bbbbbb !important;
}
.tab-prev {
  right: 67px;
}
.tab-prev:after {
  content: "";
  height: 20px;
  width: 1px;
  position: absolute;
  background-color: #bbbbbb;
  right: -0.5px;
  top: 5px;
  z-index: 1;
}
.tab-next ~ .tab-add {
  position: absolute;
  right: 0;
  top: 0;
}
.tab-remove {
  font-size: 12px !important;
  margin-right: 10px;
  top: 50%;
  transform: translateY(-50%);
  position: relative;
  float: right;
  line-height: 30px;
  margin-top: 1px;
  opacity: 0;
  color: #666;
}
.tab-remove:hover {
  color: #f90d0d;
}
.tab-remove ~ .tab-item-name {
  min-width: 38px;
  max-width: 98px;
}



