.eboard-body {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
}
.eboard-cross .eboard-canvas {
  cursor: crosshair!important;
}
.eboard-cross .eboard-canvas canvas {
  cursor: inherit!important;
}
.eboard-cursor {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  pointer-events: none;
  cursor: none;
}
.eboard-container {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  overflow: hidden;
  box-sizing: content-box !important;
  /*      background: url(data:image/png;base64,) top left no-repeat,
        url(data:image/png;base64,) top right no-repeat,
        url(data:image/png;base64,) bottom left no-repeat,
        url(data:image/png;base64,) bottom right no-repeat;
        background-size: 50px;*/
}
.eboard-pager {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  overflow: hidden;
}
.eboard-pager-hide {
  -webkit-transform: translate(-150%, -50%);
  -moz-transform: translate(-150%, -50%);
  -ms-transform: translate(-150%, -50%);
  -o-transform: translate(-150%, -50%);
  transform: translate(-150%, -50%);
}
.eboard-html {
  pointer-events: none;
  left: 0;
  top: 0;
  width: 100%;
  -webkit-transform-origin: 0 0;
  -moz-transform-origin: 0 0;
  -ms-transform-origin: 0 0;
  -o-transform-origin: 0 0;
  transform-origin: 0 0;
}
.eboard-html-canvas {
  position: absolute!important;
  left: 0;
  top: 0;
}
.eboard-html-container {
  position: absolute;
  width: 100%;
  overflow: hidden;
}
.eboard-hide {
  visibility: hidden;
}
.eboard-hide .eboard-page-show {
  visibility: hidden!important;
}
.eboard-none {
  display: none;
}
[eboard-authority="viewer"] canvas {
  cursor: default!important;
}
textarea[data-fabric-hiddentextarea] {
  left: 0!important;
}



